export default class Sorting {
	constructor( form ) {
		this.Form = form;
		this.columns = AdminColumns.Form.columns;
		this.setting = this.Form.querySelector( '[data-setting="sorting-preference"]' );
		this.select_columns = this.setting.querySelector( '[name="settings[sorting]"]' );

		this.init();
	}

	init() {
		Object.keys( this.columns ).forEach( k => {
			let column = this.columns[ k ];
			if ( Sorting.isSortable( column ) ) {
				let option = document.createElement( 'option' );
				option.value = k;

				if ( column.settings.hasOwnProperty( 'label' ) ) {
					option.innerText = column.el.querySelector( '.column_label .toggle' ).innerText;
				}

				if ( option.innerText === '' ) {
					option.innerText = column.el.querySelector( '.column_type .inner' ).innerText;
				}

				this.select_columns.appendChild( option );
			}
		} );

		let current = this.select_columns.dataset.sorting;

		this.select_columns.querySelectorAll( `[value="${current}"]` ).forEach( e => e.selected = true );
	}

	static isSortable( column ) {
		let setting = column.el.querySelector( 'table[data-setting="sort"]' );

		if ( !setting ) {
			return false;
		}

		let selected = setting.querySelectorAll( 'input[value="on"]:checked' );

		return selected.length > 0;
	}

}