let nanobus = require( 'nanobus' );

export default class HideOnScreen {
	constructor( element ) {
		this.element = element;
		this.settings = [];

		this.init();
	}

	init() {
		if ( document.querySelector( '.ac-boxes.disabled' ) ) {
			return;
		}

		this.element.querySelectorAll( '[data-setting]' ).forEach( el => {
			let setting = new Setting( el );
			setting.events.on( 'change', () => {
				this.checkDependent( setting );
			} );
			this.settings.push( setting );
		} );

		this.settings.forEach( s => s.events.emit( 'change' ) );

	}

	checkDependent( setting ) {
		const checked = setting.isChecked();
		this.settings.forEach( rel_setting => {
			if ( rel_setting.isDependentOn( setting.getName() ) ) {
				checked
					? rel_setting.disable()
					: rel_setting.enable( checked );
			}
		} )
	}
}

class Setting {

	constructor( element ) {
		this.element = element;
		this.name = element.dataset.setting;
		this.checkbox = element.querySelector( 'input[type=checkbox]' );
		this.dependentOn = element.dataset.dependent.split( ',' );
		this.events = nanobus();
		this.initEvents();
	}

	initEvents() {
		this.checkbox.addEventListener( 'change', () => {
			this.events.emit( 'change' );
		} )
	}

	disable() {
		this.element.classList.add( '-disabled' );
		this.setChecked( true );
		this.checkbox.setAttribute( 'disabled', true );
		this.events.emit( 'change' );
	}

	enable() {
		this.element.classList.remove( '-disabled' );
		this.checkbox.removeAttribute( 'disabled' );
		this.events.emit( 'change' );
	}

	setChecked( setChecked ) {
		this.checkbox.checked = setChecked;
		this.events.emit( 'change' );
	}

	getName() {
		return this.name;
	}

	isChecked() {
		return this.checkbox.checked;
	}

	isDependentOn( name ) {
		return this.dependentOn.includes( name );
	}

}