import Sidebox from "./layouts/sidebox";
import Settings from "./layouts/settings";

document.addEventListener( "DOMContentLoaded", function() {
	const layouts = this.querySelector( '.sidebox.layouts' );

	if ( layouts ) {
		AdminColumns.OrderSidebox = new Sidebox( layouts );

		// Order the Column sets menu
		const list = document.querySelector( '.layout-selector ul' );
		if ( list ) {
			AdminColumns.OrderSidebox.events.on( 'ordered', order => {
				const temp = document.createElement( 'div' );

				order.forEach( key => {
					let item = list.querySelector( `[data-screen="${key}"]` );
					if ( item ) {
						temp.appendChild( item );
					}
				} );

				temp.querySelectorAll( 'li' ).forEach( el => {
					list.appendChild( el );
				} );

			} );
		}

	}

	const settings = document.querySelector( '.ac-setbox' );
	if ( settings ) {
		AdminColumns.ListScreenSettings = new Settings( document.querySelector( '.ac-setbox' ) );
	}

	let title = document.querySelector( '#listscreen_settings input[name="title"]' );
	if ( title ) {
		title.addEventListener( 'keyup', d => {
			document.querySelectorAll( `.layout-selector [data-screen="${AC.layout}"] a` ).forEach( el => {
				el.innerHTML = title.value;
			} );
			document.querySelectorAll( `.layouts__items [data-screen="${AC.layout}"] [data-label]` ).forEach( el => {
				el.innerHTML = title.value;
			} );

		} );
	}

} );